header {
  background: #487922;
}
.top-bar {
  padding: 0;
  font-size: 12px;
  border-bottom: 4px solid #3b621c;
  min-height: 40px;
}
.main-header {
  padding: 20px 0 25px;
}
.site-title {
  color: #fff;
  margin-left: 20px;
}
.header-nav {
  background: #3b621c;
} 
.header-nav li {
  display: inline-block;
  padding: 15px 25px 15px;
}
.header-nav li a, 
.header-nav li a:visited
{
  color: bisque;
  text-decoration: none;
  display: block;
}
.header-nav li:hover {
  background-color: #487922;
}